/*------------------------------------*\
  ::HTML5 Display Definitions
  ------------------------------------*
  ::Normalize.css v3.0.0 | MIT License | git.io/normalize
\*------------------------------------*/
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
